const english = require('./_locales/en/messages.json');

let language;
const getLanguage = () => {
  if (language) {
    return language;
  }
  if (window.navigator.languages) {
    // eslint-disable-next-line prefer-destructuring
    language = window.navigator.languages[0];
  } else {
    language = window.navigator.userLanguage || window.navigator.language;
  }

  // is there a need to support eg. en-Us and en-Gb? How?
  // eslint-disable-next-line prefer-destructuring
  language = language.split('-')[0];

  try {
    // eslint-disable-next-line global-require,import/no-dynamic-require
    return require(`./_locales/${language}/messages.json`);
  } catch (e) {
    console.log(`Missing ${language} translations`);
    return english;
  }
};
language = getLanguage();

const getTranslation = (label) => language[label].message || english[label].message;

export {
  getTranslation,
};
